export default {
  name: "AboutView",
  components: {Layout},
  data() {
    return {
      companyInfo: {},
      designers: [],
    }
  },
  mounted() {
    this.getCompanyInfo()
    this.getDesigners()
  },
  methods: {
    getCompanyInfo() {
      this.getRequest("/findCompanyInfo").then(resp => {
        if (resp) {
          this.companyInfo = resp.data.data
        }
      })
    },
    getDesigners() {
      this.getRequest("/findDesigners").then(resp => {
        if (resp) {
          this.designers = resp.data.data
        }
      })
    }

  }
}

