// 观看完整视频
$('.intact-play').click(function () {
  $('#intact-play').addClass('show');
  if (parseInt(ie) == 8) {
    var intactVideoIe8 = document.getElementById('intactVideoIe8')
    intactVideoIe8.play();
  } else {
    var intactVideo = document.getElementById('intact-video')
    intactVideo.play();
  }
});

$('.close-video').click(function () {
  $('#intact-play').removeClass('show');
  if (parseInt(ie) == 8) {
    var intactVideoIe8 = document.getElementById('intactVideoIe8')
    intactVideoIe8.pause();
  } else {
    var intactVideo = document.getElementById('intact-video');
    intactVideo.pause();
  }
})


